'use client';

import { useState, useEffect, useRef, useCallback } from 'react';
import { useParams, useRouter, useSearchParams } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Textarea } from '@/components/ui/textarea';
import { Skeleton } from '@/components/ui/skeleton';
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { ThemeToggle } from '@/components/ThemeToggle';
import { ArrowLeft, Calendar, Tag, Save, Trash2 } from 'lucide-react';

interface Note {
  id: string;
  title: string;
  content: string;
  transcript?: string;
  summary?: string;
  tags: string[];
  categories: string[];
  metadata?: {
    sentiment?: string;
    keyPoints?: string[];
  };
  createdAt: string;
  updatedAt: string;
}

type SaveState = 'idle' | 'saving' | 'saved' | 'error';

export default function ViewNotePage() {
  const params = useParams();
  const router = useRouter();
  const searchParams = useSearchParams();
  const [note, setNote] = useState<Note | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Inline editing state
  const [editingTitle, setEditingTitle] = useState(false);
  const [editingContent, setEditingContent] = useState(false);
  const [editedTitle, setEditedTitle] = useState('');
  const [editedContent, setEditedContent] = useState('');
  const [hasChanges, setHasChanges] = useState(false);
  const [saveState, setSaveState] = useState<SaveState>('idle');
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  
  // Auto-save
  const saveTimeoutRef = useRef<NodeJS.Timeout | null>(null);
  const lastSavedRef = useRef<{ title: string; content: string } | null>(null);

  const noteId = params.id as string;

  useEffect(() => {
    const fetchNote = async () => {
      try {
        setLoading(true);
        const response = await fetch(`/api/notes/${noteId}`, {
          cache: 'default',
          next: { revalidate: 300 }, // Cache for 5 minutes
        });
        
        if (response.ok) {
          const data = await response.json();
          setNote(data);
          setEditedTitle(data.title);
          setEditedContent(data.content);
          lastSavedRef.current = { title: data.title, content: data.content };
        } else if (response.status === 404) {
          setError('Note not found');
        } else {
          setError('Failed to load note');
        }
      } catch (err) {
        setError('Error loading note');
        console.error('Error fetching note:', err);
      } finally {
        setLoading(false);
      }
    };

    if (noteId) {
      fetchNote();
    }
  }, [noteId]);

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  };

  const handleBack = () => {
    // Check if there's a return URL parameter
    const returnUrl = searchParams.get('return');
    if (returnUrl) {
      router.push(returnUrl);
    } else {
      // Default to home page with review view
      router.push('/?view=review');
    }
  };

  const handleNavigation = (view: 'take' | 'review' | 'chat') => {
    router.push(`/?view=${view}`);
  };

  // Check for changes
  useEffect(() => {
    if (note && lastSavedRef.current) {
      const hasTitleChanged = editedTitle !== lastSavedRef.current.title;
      const hasContentChanged = editedContent !== lastSavedRef.current.content;
      setHasChanges(hasTitleChanged || hasContentChanged);
    }
  }, [editedTitle, editedContent, note]);

  // Auto-save function
  const saveNote = useCallback(async () => {
    if (!note || !hasChanges) return;

    try {
      setSaveState('saving');
      const response = await fetch(`/api/notes/${noteId}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          title: editedTitle,
          content: editedContent,
        }),
      });

      if (response.ok) {
        const updatedNote = await response.json();
        setNote(updatedNote);
        lastSavedRef.current = { title: editedTitle, content: editedContent };
        setHasChanges(false);
        setSaveState('saved');
        
        // Reset to idle after showing saved state
        setTimeout(() => setSaveState('idle'), 2000);
      } else {
        setSaveState('error');
        setTimeout(() => setSaveState('idle'), 3000);
      }
    } catch (err) {
      console.error('Error saving note:', err);
      setSaveState('error');
      setTimeout(() => setSaveState('idle'), 3000);
    }
  }, [note, noteId, editedTitle, editedContent, hasChanges]);

  // Auto-save with debouncing
  useEffect(() => {
    if (hasChanges) {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
      
      saveTimeoutRef.current = setTimeout(() => {
        saveNote();
      }, 1000); // Auto-save after 1 second of no changes
    }

    return () => {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
    };
  }, [hasChanges, saveNote]);

  // Manual save function
  const handleManualSave = () => {
    if (saveTimeoutRef.current) {
      clearTimeout(saveTimeoutRef.current);
    }
    saveNote();
  };

  // Dynamic save button component
  const SaveButton = () => {
    const getButtonProps = () => {
      switch (saveState) {
        case 'saving':
          return {
            variant: 'default' as const,
            className: 'bg-blue-500 hover:bg-blue-600 text-white animate-pulse',
            children: (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                Saving...
              </>
            ),
          };
        case 'saved':
          return {
            variant: 'default' as const,
            className: 'bg-green-500 hover:bg-green-600 text-white',
            children: (
              <>
                <Save className="h-4 w-4 mr-2" />
                Saved
              </>
            ),
          };
        case 'error':
          return {
            variant: 'destructive' as const,
            className: 'bg-red-500 hover:bg-red-600 text-white',
            children: (
              <>
                <Save className="h-4 w-4 mr-2" />
                Error
              </>
            ),
          };
        default:
          return {
            variant: hasChanges ? ('default' as const) : ('outline' as const),
            className: hasChanges 
              ? 'bg-yellow-500 hover:bg-yellow-600 text-white' 
              : 'text-muted-foreground',
            children: (
              <>
                <Save className="h-4 w-4 mr-2" />
                {hasChanges ? 'Save' : 'Saved'}
              </>
            ),
          };
      }
    };

    const buttonProps = getButtonProps();

    return (
      <Button
        variant={buttonProps.variant}
        size="sm"
        onClick={handleManualSave}
        disabled={!hasChanges && saveState === 'idle'}
        className={buttonProps.className}
      >
        {buttonProps.children}
      </Button>
    );
  };

  const handleDelete = async () => {
    try {
      const response = await fetch(`/api/notes/${noteId}`, {
        method: 'DELETE',
      });

      if (response.ok) {
        setDeleteDialogOpen(false);
        router.push('/');
      } else {
        alert('Failed to delete note');
      }
    } catch (err) {
      console.error('Error deleting note:', err);
      alert('Error deleting note');
    }
  };

  if (loading) {
    return (
      <div className="h-screen w-screen bg-background flex flex-col overflow-hidden">
        {/* Header */}
        <header className="border-b flex-shrink-0">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
              <h1 className="text-2xl font-bold">Polynote AI</h1>
              <div className="flex items-center space-x-4">
                <nav className="flex space-x-4">
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('take')}
                  >
                    Take Notes
                  </Button>
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('review')}
                  >
                    Review Notes
                  </Button>
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('chat')}
                  >
                    AI Chat
                  </Button>
                </nav>
                <ThemeToggle />
              </div>
            </div>
          </div>
        </header>

        {/* Main Content */}
        <main className="flex-1 overflow-auto p-4">
          <div className="max-w-4xl mx-auto space-y-6">
            {/* Header Skeleton */}
            <div className="flex items-center gap-4">
              <Skeleton className="h-10 w-10" />
              <div className="space-y-2">
                <Skeleton className="h-8 w-64" />
                <Skeleton className="h-4 w-32" />
              </div>
            </div>

            {/* Content Skeleton */}
            <Card>
              <CardHeader>
                <Skeleton className="h-8 w-3/4" />
                <Skeleton className="h-4 w-1/2" />
              </CardHeader>
              <CardContent className="space-y-4">
                <Skeleton className="h-4 w-full" />
                <Skeleton className="h-4 w-5/6" />
                <Skeleton className="h-4 w-4/5" />
                <Skeleton className="h-4 w-3/4" />
              </CardContent>
            </Card>
          </div>
        </main>
      </div>
    );
  }

  if (error || !note) {
    return (
      <div className="h-screen w-screen bg-background flex flex-col overflow-hidden">
        {/* Header */}
        <header className="border-b flex-shrink-0">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
            <div className="flex justify-between items-center h-16">
              <h1 className="text-2xl font-bold">Polynote AI</h1>
              <div className="flex items-center space-x-4">
                <nav className="flex space-x-4">
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('take')}
                  >
                    Take Notes
                  </Button>
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('review')}
                  >
                    Review Notes
                  </Button>
                  <Button
                    variant="ghost"
                    onClick={() => handleNavigation('chat')}
                  >
                    AI Chat
                  </Button>
                </nav>
                <ThemeToggle />
              </div>
            </div>
          </div>
        </header>

        {/* Main Content */}
        <main className="flex-1 overflow-auto p-4">
          <div className="max-w-4xl mx-auto">
            <div className="flex items-center gap-4 mb-6">
              <Button variant="outline" size="icon" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4" />
              </Button>
              <h1 className="text-2xl font-bold">Error</h1>
            </div>
            
            <Card>
              <CardContent className="p-8 text-center">
                <p className="text-muted-foreground mb-4">
                  {error || 'Note not found'}
                </p>
                <Button onClick={handleBack}>
                  <ArrowLeft className="h-4 w-4 mr-2" />
                  Go Back
                </Button>
              </CardContent>
            </Card>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="h-screen w-screen bg-background flex flex-col overflow-hidden">
      {/* Header */}
      <header className="border-b flex-shrink-0">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <h1 className="text-2xl font-bold">Polynote AI</h1>
            <div className="flex items-center space-x-4">
              <nav className="flex space-x-4">
                <Button
                  variant="ghost"
                  onClick={() => handleNavigation('take')}
                >
                  Take Notes
                </Button>
                <Button
                  variant="ghost"
                  onClick={() => handleNavigation('review')}
                >
                  Review Notes
                </Button>
                <Button
                  variant="ghost"
                  onClick={() => handleNavigation('chat')}
                >
                  AI Chat
                </Button>
              </nav>
              <ThemeToggle />
            </div>
          </div>
        </div>
      </header>

      {/* Main Content */}
      <main className="flex-1 overflow-auto p-4">
        <div className="max-w-4xl mx-auto space-y-6">
          {/* Note Header */}
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Button variant="outline" size="icon" onClick={handleBack}>
                <ArrowLeft className="h-4 w-4" />
              </Button>
              <div className="flex-1">
                {editingTitle ? (
                  <Input
                    value={editedTitle}
                    onChange={(e) => setEditedTitle(e.target.value)}
                    onBlur={() => setEditingTitle(false)}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        setEditingTitle(false);
                      }
                      if (e.key === 'Escape') {
                        setEditedTitle(note.title);
                        setEditingTitle(false);
                      }
                    }}
                    className="text-2xl font-bold bg-muted/50 border-none shadow-none p-2 h-auto focus-visible:ring-0 rounded"
                    autoFocus
                  />
                ) : (
                  <h1 
                    className="text-2xl font-bold cursor-pointer hover:bg-muted/50 p-2 -m-2 rounded"
                    onClick={() => setEditingTitle(true)}
                  >
                    {editedTitle}
                  </h1>
                )}
                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <Calendar className="h-4 w-4" />
                  <span>Created {formatDate(note.createdAt)}</span>
                  {note.updatedAt !== note.createdAt && (
                    <>
                      <span>•</span>
                      <span>Updated {formatDate(note.updatedAt)}</span>
                    </>
                  )}
                </div>
              </div>
            </div>
            
            <div className="flex items-center gap-2">
              <SaveButton />
              <Dialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
                <DialogTrigger asChild>
                  <Button variant="outline" size="sm">
                    <Trash2 className="h-4 w-4 mr-2" />
                    Delete
                  </Button>
                </DialogTrigger>
                <DialogContent>
                  <DialogHeader>
                    <DialogTitle>Delete Note</DialogTitle>
                    <DialogDescription>
                      Are you sure you want to delete "{note.title}"? This action cannot be undone.
                    </DialogDescription>
                  </DialogHeader>
                  <DialogFooter>
                    <Button variant="outline" onClick={() => setDeleteDialogOpen(false)}>
                      Cancel
                    </Button>
                    <Button variant="destructive" onClick={handleDelete}>
                      <Trash2 className="h-4 w-4 mr-2" />
                      Delete
                    </Button>
                  </DialogFooter>
                </DialogContent>
              </Dialog>
            </div>
          </div>

        {/* Tags and Categories */}
        {(note.tags.length > 0 || note.categories.length > 0) && (
          <div className="flex flex-wrap gap-2">
            {note.tags.map((tag) => (
              <Badge key={tag} variant="secondary" className="flex items-center gap-1">
                <Tag className="h-3 w-3" />
                {tag}
              </Badge>
            ))}
            {note.categories.map((category) => (
              <Badge key={category} variant="outline">
                {category}
              </Badge>
            ))}
          </div>
        )}

        {/* Summary */}
        {note.summary && (
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Summary</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground">{note.summary}</p>
            </CardContent>
          </Card>
        )}

        {/* Main Content */}
        <Card>
          <CardHeader>
            <CardTitle className="text-lg">Content</CardTitle>
          </CardHeader>
          <CardContent>
            {editingContent ? (
              <Textarea
                value={editedContent}
                onChange={(e) => setEditedContent(e.target.value)}
                onBlur={() => setEditingContent(false)}
                onKeyDown={(e) => {
                  if (e.key === 'Escape') {
                    setEditedContent(note.content);
                    setEditingContent(false);
                  }
                }}
                className="min-h-[200px] resize-none bg-muted/50 border-none shadow-none p-4 focus-visible:ring-0 rounded"
                autoFocus
              />
            ) : (
              <div 
                className="prose prose-sm max-w-none dark:prose-invert cursor-pointer hover:bg-muted/50 p-4 -m-4 rounded"
                onClick={() => setEditingContent(true)}
              >
                <div className="whitespace-pre-wrap">{editedContent}</div>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Transcript */}
        {note.transcript && (
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Transcript</CardTitle>
              <CardDescription>
                Original voice recording transcript
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="prose prose-sm max-w-none dark:prose-invert">
                <div className="whitespace-pre-wrap text-muted-foreground">
                  {note.transcript}
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Metadata */}
        {note.metadata && (
          <Card>
            <CardHeader>
              <CardTitle className="text-lg">Analysis</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {note.metadata.sentiment && (
                <div>
                  <h4 className="font-medium mb-2">Sentiment</h4>
                  <Badge variant="outline">{note.metadata.sentiment}</Badge>
                </div>
              )}
              
              {note.metadata.keyPoints && note.metadata.keyPoints.length > 0 && (
                <div>
                  <h4 className="font-medium mb-2">Key Points</h4>
                  <ul className="list-disc list-inside space-y-1 text-sm text-muted-foreground">
                    {note.metadata.keyPoints.map((point, index) => (
                      <li key={index}>{point}</li>
                    ))}
                  </ul>
                </div>
              )}
            </CardContent>
          </Card>
        )}
        </div>
      </main>
    </div>
  );
}
